import React from 'react';

// images
import arrow from './img/download-blue.svg';;

const DownloadWhite = () => (
  <button className="download-white">
    <img src={arrow} alt="download arrow" />
    Firefox Developer Edition
  </button>
);

export default DownloadWhite;



